import React, { Component } from 'react';
import Modal from 'bee-modal';
import Button from '../../src';

 class Demo7 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showModal: false
        }
        this.close = this.close.bind(this);
        this.open = this.open.bind(this);
    }

    close() {
        this.setState({
            showModal: false
        });
    }

    open() {
        this.setState({
            showModal: true
        });
    }

    render() {
        return (
            <div>
                <Button colors="primary" onClick={ this.open }></Button>
                <Modal
                    show = { this.state.showModal }
                    onHide = { this.close } >
                        <Modal.Header closeButton>
                            <Modal.Title></Modal.Title>
                        </Modal.Header>

                        <Modal.Body>
                            
                        </Modal.Body>

                        <Modal.Footer>
                            <Button onClick={ this.close } shape="border" style={{marginRight: 8}}></Button>
                            <Button onClick={ this.close } colors="primary"></Button>
                        </Modal.Footer>
                </Modal>
            </div>
        )
    }
}

export default Demo7;


